.main{

    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    background-color: black;
    color: black;

    height: 100vh;
  
    .mask, .body{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #e7eaab;
      font-size: 64px;
      line-height: 66px;
      cursor: default;
      
      p{
        width: 1000px;
        padding:40px;
      }
      span{
        color: #004445;
      }
    }
  
    .mask{
      mask-image: url('../../public/mask.svg');
      mask-repeat: no-repeat;
      mask-size: 40px;
      background: #6FB98F;
      position: absolute;
      color: black;
    }

    // background: '#004445',
    // pointBasic: '#6FB98F',
    // pointDark: '#2C7873',
    // pointBright: '#e7eaab',
}